<template>
  <div class="rider-data chart-item">
    <div class="title">
      外卖骑手概况
      <div class="sub-title">骑手增长率</div>
    </div>
    <my-echart height="156px" :options="options"></my-echart>
  </div>
</template>

<script>
import MyEchart from "@/components/MyEchart.vue";
export default {
  name: "rider-data",
  props: {
    riderData: {
      type: Object,
      default: () => ({}),
    },
  },
  components: {
    MyEchart,
  },
  data() {
    return {
      options: {},
      index: 0,
      type: "orderData",
    };
  },
  methods: {
    updateOptions() {
      this.index++;
      this.index = this.index % 2;
      this.type = this.index ? "orderData" : "rateData";
      let data = this.riderData[this.type];
      this.options = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
          },
          showContent: false,
        },
        color: ["#D1F88B", "#73C9F5"],
        grid: {
          top: 30,
          left: '0%',
          right: '0%',
          bottom: '0%',
          containLabel: true,
        },
        legend: {
          type: "plain",
          right: 0,
          icon: "rect",
          itemWidth: 18,
          itemHeight: 12,
          itemGap: 20,
          textStyle: {
            fontSize: 12,
            color: ["#D1F88B", "#73C9F5"],
          },
        },
        xAxis: {
          type: "category",
          // boundaryGap: false,
          data: this.riderData.axisX,
          axisTick: {
            show: false,
          },
          axisLabel: {
            fontSize: 12,
            color: "#6D777F",
            interval:0,
          },
          axisLine: {
            onZero: false,
            lineStyle: {
              color: "#6D777F",
            },
          },
        },
        yAxis: {
          type: "value",
          axisTick: {
            show: false,
          },
          axisLabel: {
            fontSize: 12,
            color: "#6D777F",
          },
          axisLine: {
            show: true,
            onZero: false,
            lineStyle: {
              color: "#6D777F",
            },
          },
          splitLine: {
            lineStyle: {
              type: "dotted",
            },
          },
        },
        series: [
          {
            name: data.legend1,
            type: "line",
            data: data.data1,
            lineStyle: {
              width: 1,
            },
            symbol: "none",
            smooth: true,
          },
          {
            name: data.legend2,
            type: "line",
            data: data.data2,
            lineStyle: {
              width: 1,
            },
            symbol: "none",
            smooth: true,
          },
        ],
      };
    },
  },
  watch: {
    riderData: {
      handler() {
        this.updateOptions();
        setInterval(this.updateOptions, 5000);
      },
      immediate: true,
      deep: true,
    },
  },
};
</script>

<style lang="scss" scoped></style>
