<template>
  <div class="hot-categoty chart-item">
    <div class="top">
      <div class="title">
        当前热卖品类
        <div class="sub-title">热卖品类</div>
      </div>
      <div class="right">最后更新时间：{{ date }} {{ time }}</div>
    </div>
    <my-echart height="156px" :options="options"></my-echart>
  </div>
</template>

<script>
import MyEchart from "@/components/MyEchart.vue";
export default {
  name: "hot-categoty",
  props: {
    categoryData: {
      type: Object,
      default: () => ({}),
    },
  },
  components: {
    MyEchart,
  },
  data() {
    return {
      options: {},
      index: 0,
      type: "data1",
    };
  },
  methods: {
    updateOptions() {
      this.index++;
      this.index = this.index % 2;
      this.type = this.index ? "data1" : "data2";
      let data = this.categoryData[this.type];
      this.options = {
        color: "#D1F88A",
        tooltip: {
          trigger: "axis",
          backgroundColor: "rgba(0,0,0,0.6)",
          borderWidth: 0,
          // alwaysShowContent: true,
          formatter: (params) => {
            // console.log(params);
            return `
              <div class="category-formatter">
                <div class="name">${params[0].name}</div>
                <div class="item"><i class='point' style='background:#D1F88A'></i> 国内：${data.data1[params[0].dataIndex]}</div>
                <div class="item"><i class='point' style='background:#D1F88A'></i> 海外：${data.data2[params[0].dataIndex]}</div>
              </div>
            `;
          },
          axisPointer: {
            type: "shadow",
          },
          textStyle: {
            color: "#fff",
            fontSize: 12,
            fontWeight: "normal",
          },
        },
        grid: {
          top: 20,
          left: '0%',
          right: '0%',
          bottom: 0,
          containLabel: true,
        },
        xAxis: {
          type: "category",
          data: data.axisX,
          axisTick: {
            show: false,
          },
          axisLabel: {
            fontSize: 12,
            color: "#6D777F",
          },
          axisLine: {
            onZero: false,
            lineStyle: {
              color: "#6D777F",
            },
          },
        },
        yAxis: {
          type: "value",
          data: [""],
          axisTick: {
            show: false,
          },
          axisLabel: {
            fontSize: 12,
            color: "#6D777F",
          },
          axisLine: {
            onZero: false,
            show: true,
            lineStyle: {
              color: "#6D777F",
            },
          },
          splitLine: {
            lineStyle: {
              type: "dotted",
            },
          },
        },
        series: [
          {
            name: "国内",
            data: data.data1,
            type: "bar",
            showBackground: true,
            backgroundStyle: {
              color: "rgba(180, 180, 180, 0.2)",
            },
          },
        ],
      };
    },
  },
  watch: {
    categoryData: {
      handler(newData) {
        this.updateOptions();
        setInterval(this.updateOptions, 5000);
      },
      immediate: true,
      deep: true,
    },
  },
};
</script>
<style lang="scss">
.category-formatter{
  .item{
    display: flex;
    align-items: center;
  }
}
</style>
<style lang="scss" scoped>
.top {
  display: flex;
  justify-content: space-between;
  .right {
    font-size: 12px;
    margin-top: 5px;
  }
}
</style>
