import Vue from 'vue';

Vue.mixin({
  data() {
    return {
      date: '',
      time: '',
    }
  },
  created() {
    this.updateTime();
  },
  methods: {
    updateTime() {
      this.date = this.$moment().format('YYYY-MM-DD');
      this.time = this.$moment().format('HH:mm:ss');
      setTimeout(this.updateTime, 1000);
    },
    numFormat(val){
      return String(val).replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
    }
  },
});