<template>
  <div class="calendar-data chart-item">
    <my-echart height="160px" :options="options"></my-echart>
  </div>
</template>

<script>
import MyEchart from "@/components/MyEchart.vue";
export default {
  name: "calendar-data",
  // props: {
  //   ageData: {
  //     type: Object,
  //     default: () => ({}),
  //   },
  // },
  components: {
    MyEchart,
  },
  data() {
    return {
      options: {},
    };
  },
  created() {
    this.updateOptions();
  },
  methods: {
    getVirtulData(year) {
      year = year || "2017";
      var date = +this.$echarts.number.parseDate(year + "-01-01");
      var end = +this.$echarts.number.parseDate(+year + 1 + "-01-01");
      var dayTime = 3600 * 24 * 1000;
      var data = [];
      for (var time = date; time < end; time += dayTime) {
        data.push([
          this.$echarts.format.formatTime("yyyy-MM-dd", time),
          Math.floor(Math.random() * 10000),
        ]);
      }
      return data;
    },
    updateOptions() {
      var data = this.getVirtulData(2021);

      this.options = {
        // backgroundColor: "#404a59",
        // title: {
        //   top: 30,
        //   text: "车辆故障分析",
        //   subtext: "数据纯属虚构",
        //   left: "center",
        //   textStyle: {
        //     color: "#fff",
        //   },
        // },
        // tooltip: {
        //   trigger: "item",
        // },
        // legend: {
        //   top: "30",
        //   left: "100",
        //   data: ["步数", "Top 12"],
        //   textStyle: {
        //     color: "#fff",
        //   },
        // },
        calendar: [
          {
            top: "12%",
            left: "6%",
            right: "0%",
            cellSize: "auto",
            range: ["2021-01-01", "2021-12-31"],
            splitLine: {
              show: true,
              lineStyle: {
                color: "#000",
                width: 2,
                type: "solid",
              },
            },
            itemStyle: {
              normal: {
                color: "#323c48",
                borderWidth: 1,
                borderColor: "#666",
              },
            },
            dayLabel: {
              nameMap: "cn",
              color: "#fff",
              fontSize: 12,
            },
            monthLabel: {
              nameMap: "cn",
              color: "#fff",
              fontSize: 12,
            },
            yearLabel: {
              formatter: "{start}年",
              textStyle: {
                color: "#fff",
                fontSize: 16,
              },
            },
          },
        ],
        series: [
          {
            name: "步数",
            type: "scatter",
            coordinateSystem: "calendar",
            data: data,
            symbolSize: (val) => val[1] / 1000,
            itemStyle: {
              normal: {
                color: "#ddb926",
              },
            },
          },
          {
            name: "Top 100",
            type: "effectScatter",
            coordinateSystem: "calendar",
            data: data.sort((a, b) => b[1] - a[1]).slice(0, 60),
            symbolSize: (val) => val[1] / 1000,
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke",
            },
            hoverAnimation: true,
            itemStyle: {
              normal: {
                color: "#f4e925",
                shadowBlur: 10,
                shadowColor: "#333",
              },
            },
            zlevel: 1,
          },
        ],
      };
    },
  },
  // watch:{
  //   ageData:{
  //     handler(newData){
  //       this.options = {

  //       }
  //     },
  //     immediate: true,
  //     deep: true,
  //   }
  // }
};
</script>

<style lang="scss" scoped></style>
