<template>
  <div class="area-top chart-item">
    <div class="title">地区商家销售排行</div>
    <div class="area">
      <template>
        <div v-for="(item, i) in currentData" :key="item.city + i" class="item">
          <div class="city">
            <strong>{{ item.city }}</strong>
          </div>
          <div class="area-chart">
            <div class="img">
              <img :src="options[i].img" alt="" />
            </div>
            <my-echart height="128px" :options="options[i].option"></my-echart>
          </div>
          <dv-scroll-board
            :config="configs[i]"
            style="width:100%;height:160px"
          />
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import MyEchart from "@/components/MyEchart.vue";
export default {
  name: "area-top",
  components: {
    MyEchart,
  },
  props: {
    areaTop: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      configs: [],
      options: [],
      currentData: [],
      index: 0,
    };
  },
  computed: {},
  methods: {
    getCurrentData() {
      this.currentData = this.areaTop.slice(this.index, this.index + 3);
      if (this.index > this.areaTop.length - 3) {
        this.index = 0;
      } else {
        this.index++;
      }
      if (this.currentData.length == 1) {
        this.currentData.push(this.areaTop[0]);
        this.currentData.push(this.areaTop[1]);
      }
      if (this.currentData.length == 2) {
        this.currentData.push(this.areaTop[0]);
      }
    },
    updateConfigs() {
      let configData = [];
      //  order:712   sales:7540   shop:"麦当劳"
      this.currentData.forEach((item) => {
        let arr = [];
        item.shop.forEach((shop) => {
          arr.push([
            `<span style="color:#FFF; font-size:12px">${shop.shop}</span>`,
            `<span style="color:#B2D17E; font-size:12px">${shop.order}</span>`,
            `<span style="color:#FFF; font-size:12px">${shop.sales}</span>`,
          ]);
        });
        configData.push(arr);
      });
      let configs = [];
      configData.forEach((data) => {
        configs.push({
          header: [
            `<span style="color:#FFF; font-size:12px">商家</span>`,
            `<span style="color:#FFF; font-size:12px">订单</span>`,
            `<span style="color:#FFF; font-size:12px">销售额</span>`,
          ],
          data,
          rowNum: 4,
          index: false,
          // columnWidth: [50],
          align: ["center"],
          headerBGC: "#5A5A5A",
          oddRowBGC: "#373737",
          evenRowBGC: "#282828",
          waitTime: 2000,
          headerHeight: 35,
          indexHeader: "",
          carousel: "single",
        });
      });
      this.configs = configs;
    },
    updateOptions() {
      let optionData = [];
      this.currentData.forEach((item) => {
        let rate = Math.abs(Number.parseInt(item.rate));
        optionData.push({
          city: item.city,
          data: [100 - rate, rate],
        });
      });
      let options = [];
      optionData.forEach((item, i) => {
        options.push({
          img: require(`@/assets/imgs/area${i+1}.png`),
          option: {
            // backgroundColor: '#fff',
            color: ["#D2F494", "#4F4F4F"],
            title: {
              text: item.data[0] + "%",
              top: "60%",
              left: "48%",
              textAlign: "center",
              textStyle: {
                color: "#D2F494",
                fontSize: 12,
              },
            },
            grid: {
              top: "5%",
              bottom: "5%",
              left: "5%",
              right: "5%",
            },
            series: [
              {
                name: "访问来源",
                type: "pie",
                radius: ["60%", "80%"],
                center: ["50%", "50%"],
                avoidLabelOverlap: false,
                label: {
                  show: false,
                },
                labelLine: {
                  show: false,
                },
                data: item.data,
              },
            ],
          },
        });
      });
      this.options = options;
    },
  },
  watch: {
    areaTop: {
      handler(newData) {
        this.getCurrentData();
        this.updateConfigs();
        this.updateOptions();
        setInterval(() => {
          this.getCurrentData();
          this.updateConfigs();
          this.updateOptions();
        }, 5000);
      },
      immediate: true,
      deep: true,
    },
  },
};
</script>

<style lang="scss" scoped>
.area-top {
  .title {
    text-align: center;
    margin-bottom: 10px;
  }
  .area {
    display: flex;
    .item {
      flex-basis: calc(100% / 3);
      .city {
        text-align: center;
        background-color: #3c3d40;
        padding: 7.5px 0;
        font-size: 14px;
      }
      .area-chart{
        position: relative;
        .img{
          width: 100%;
          height: 50px;
          position: absolute;
          top: 25%;
          left: 0;
          right: 0;
          display: flex;
          align-items: center;
          justify-content: center;      
        }
      }
    }
    .item:nth-child(2) {
      margin: 0 10px;
    }
  }
}
</style>
