<template>
  <div class="user-device chart-item">
    <div class="left">
      <my-echart height="96px" :options="options"></my-echart>
    </div>
    <div class="right">
      <div class="top">
        <div class="title">
          外卖登录设备
          <div class="sub-title">设备分布图</div>
        </div>
        <div class="number">
          {{ deviceData.totalDevices | numFormat
          }}<span class="sub-text">台</span>
        </div>
      </div>
      <ul class="bottom">
        <li
          v-for="(item, i) in deviceData.devices"
          :key="item.value"
          class="item"
        >
          <div class="value">{{ item.value | numFormat }}</div>
          <div class="key">
            <div class="point" :style="{ background: colors[i] }"></div>
            {{ item.key }}
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import MyEchart from "@/components/MyEchart.vue";
export default {
  name: "user-device",
  props: {
    deviceData: {
      type: Object,
      default: () => ({}),
    },
  },
  components: {
    MyEchart,
  },
  data() {
    return {
      options: {},
      colors: ["#B0CF78", "#9DC35B", "#83A748"],
    };
  },
  watch: {
    deviceData: {
      handler(newData) {
        this.options = {
          color: this.colors,
          tooltip: {
            show: false,
          },
          grid: {
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            containLabel: true,
          },
          series: [
            {
              name: "面积模式",
              type: "pie",
              radius: [0, 35],
              center: ["40%", "50%"],
              roseType: "area",
              labelLine: {
                show: false,
              },
              data: this.deviceData.devices,
            },
          ],
        };
      },
      immediate: true,
      deep: true,
    },
  },
};
</script>

<style lang="scss" scoped>
.user-device {
  display: flex;
  .right {
    flex-grow: 1;
    .top {
      display: flex;
      justify-content: space-between;
    }
    .bottom {
      display: flex;
      flex-grow: 1;
      justify-content: space-between;
      font-weight: 700;
      margin-top: 10px;
      font-size: 14px;
      .key {
        font-size: 12px;
        display: flex;
        align-items: center;
      }
    }
  }
}
</style>
