<template>
  <div class="chart-item">
    <dv-border-box-8 :color="['rgb(66, 68, 70)', 'rgb(251,253,142)']" :dur="5">
      <div class="real-time-order">
        <div class="left">
          <div class="title">
            实时订单趋势图
            <div class="sub-title">实时订单数量</div>
          </div>
          <div class="number">
            {{ order[order.length - 1] | numFormat
            }}<span class="sub-text">万</span>
          </div>
          <div class="growth">
            <div>同比增长率</div>
            <!-- <div class="rate">{{ "2.32" }}%</div> -->
            <div class="rate">
              {{
                (
                  ((order[order.length - 1] - order[order.length - 2]) * 100) /
                  order[order.length - 1]
                ).toFixed(2)
              }}%
            </div>
          </div>
        </div>
        <my-echart height="200px" :options="options"></my-echart>
      </div>
    </dv-border-box-8>
  </div>
</template>

<script>
import MyEchart from "@/components/MyEchart.vue";
export default {
  name: "real-time-order",
  props: {
    realTimeOrder: {
      type: Number,
      default: 1992,
    },
  },
  components: {
    MyEchart,
  },
  data() {
    return {
      options: {},
      times: [],
      order: [],
    };
  },
  created() {
    // 模拟十条数据
    for (var i = 0; i < 12; i++) {
      this.times.push(
        this.$moment()
          .add(-110 + i * 10, "s")
          .format("HH:mm:ss")
      );
      let order = 0;
      if (Math.random() > 0.5) {
        order = this.realTimeOrder * 1 + Math.floor(Math.random() * 1500);
      } else {
        order = this.realTimeOrder - Math.floor(Math.random() * 1500);
      }
      this.order.push(order);
    }
    this.updateOptions();

    setInterval(() => {
      this.getData();
      this.updateOptions();
    }, 1000 * 10);
  },
  methods: {
    getData() {
      let order = 0;
      if (Math.random() > 0.5) {
        order = this.realTimeOrder * 1 + Math.floor(Math.random() * 1000);
      } else {
        order = this.realTimeOrder - Math.floor(Math.random() * 1000);
      }
      this.times.push(
        this.$moment()
          .add(10, "s")
          .format("HH:mm:ss")
      );
      this.order.push(order);
      this.times = this.times.slice(1);
      this.order = this.order.slice(1);
    },
    updateOptions() {
      this.options = {
        color: "#D1F88B",
        grid: {
          top: "5%",
          left: "0%",
          right: "1%",
          bottom: "20%",
          containLabel: true,
        },
        tooltip: {
          trigger: "axis",
          backgroundColor: "rgba(0,0,0,0.6)",
          borderWidth: 0,
          // alwaysShowContent: true,
          formatter: (params) => {
            // console.log(params);
            return `
              <div class="category-formatter">
                <div class="name">${params[0].seriesName}  ${
              params[0].name
            }</div>
                <div class="item"><i class='point' style='background:#D1F88A'></i>    ${this.numFormat(
                  this.order[params[0].dataIndex]
                )}万</div>
              </div>
            `;
          },
          axisPointer: {
            type: "line",
          },
          textStyle: {
            color: "#fff",
            fontSize: 12,
            fontWeight: "normal",
          },
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: this.times,
          axisTick: {
            show: true,
            alignWithLabel: true,
          },
          axisLabel: {
            fontSize: 12,
            color: "#6D777F",
            interval: 0,
          },
          axisLine: {
            onZero: false,
            lineStyle: {
              color: "#6D777F",
            },
          },
        },
        yAxis: {
          type: "value",
          axisTick: {
            show: true,
          },
          axisLabel: {
            fontSize: 12,
            color: "#6D777F",
          },
          axisLine: {
            show: true,
            onZero: false,
            lineStyle: {
              color: "#6D777F",
            },
          },
          splitLine: {
            lineStyle: {
              type: "solid",
            },
          },
        },
        dataZoom: [
          {
            type: "inside",
            start: 0,
            end: 100,
          },
          {
            start: 0,
            end: 100,
            height: 16,
            bottom: "5%",
            left: "5%",
            right: "1%",
            handleIcon:
              "M 0.0525 0.5656 L 0.0525 0 L -0.0583 0 L -0.0583 0.5656 L -0.2449 0.5656 L -0.2449 1.4344 L -0.0525 1.4344 L -0.0525 2 L 0.0525 2 L 0.0525 1.4344 L 0.2449 1.4344 L 0.2449 0.5656 L 0.0525 0.5656 Z M 0.1399 1.1953 L -0.1458 1.1953 L -0.1458 1.1137 L 0.1399 1.1137 L 0.1399 1.1953 Z M 0.1399 0.8863 L -0.1458 0.8863 L -0.1458 0.8047 L 0.1399 0.8047 L 0.1399 0.8863 Z",
            handleSize: "100%",
            handleStyle: {
              color: "#a7b7cc",
            },
            textStyle: {
              color: "rgb(200, 200, 200)",
            },
            fillerColor: "rgba(120,126,134,.3)",
            dataBackground: {
              lineStyle: {
                color: "grey",
              },
              areaStyle: {
                color: "gray",
              },
            },
            borderColor: "rgb(200, 200, 200)",
          },
        ],
        series: [
          {
            name: "实时订单",
            type: "line",
            data: this.order,
            lineStyle: {
              width: 2,
            },
            // symbol: "none",
            smooth: true,
          },
        ],
      };
    },
  },
  // watch: {
  //   realTimeOrder: {
  //     handler(newData) {
  //     },
  //     immediate: true,
  //     deep: true,
  //   },
  // },
};
</script>

<style lang="scss" scoped>
.chart-item {
  padding: 0 !important;
}
.real-time-order {
  padding: 10px;
  display: flex;
  .left {
    flex-basis: 20%;
    .growth {
      .rate {
        color: #c5fb79;
        font-weight: 700;
      }
    }
  }
}
</style>
