<template>
  <div class="user-age chart-item">
    <div class="top">
      <div class="title">
        外卖用户年龄分布&平均年龄
        <div class="sub-title">年龄分布</div>
      </div>
      <div class="number">
        {{ ageData.averageAge }}<span class="sub-text">岁</span>
      </div>
    </div>
    <my-echart height="48px" :options="options"></my-echart>
    <ul class="bottom">
      <li v-for="(item, i) in ageData.ages" :key="item.value" class="item">
        <div class="value">{{ item.value | numFormat }}</div>
        <div class="key">
          <div class="point" :style="{ background: colors[i] }"></div>
          {{ item.key }}
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import MyEchart from "@/components/MyEchart.vue";
export default {
  name: "user-age",
  props: {
    ageData: {
      type: Object,
      default: () => ({}),
    },
  },
  components: {
    MyEchart,
  },
  data() {
    return {
      options: {},
      colors:['#557c02','#7db408','#99dd08','#aeff00'],//'#74A631','#BEF563','#CAFC89','#FBFD8E'
    };
  },
  watch: {
    ageData: {
      handler(newData) {
        let series = [];
        if (newData.ages) {
          newData.ages.forEach((item, i) => {
            series.push({
              name: item.key,
              type: "bar",
              stack: "总量",
              data: [item.value],
              barWidth: 10,
              itemStyle:{
                color: this.colors[i],
              }
            });
          });
        }

        this.options = {
          tooltip: {
            trigger: 'axis',
            formatter:(params)=>{
              // console.log('params=',params);
              let htmlStr = `
                <ul class="age-formatter">
                  <div class="age-title">年龄分布</div>
              `;
              for(let i = 0; i < params.length; i++){
                let item = params[i];
                htmlStr += `
                <li class="item">
                    <div class="point" style="background: ${item.color}"></div>
                    ${ item.seriesName} : ${String(item.data).replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')}
                </li>   
                `
              }
              htmlStr += `</ul>`;
              return htmlStr;
            },
            backgroundColor:'rgba(0,0,0,0.6)',
            borderWidth:0,
            axisPointer: {
                type: 'shadow'
            },
            textStyle:{
                color: '#fff',
                fontSize: 12,
                lineHiehgt: 1,
                fontWeight: 'normal',
            }
          },
          grid: {
            left: '5%',
            right: '5%',
            top: "5%",
            bottom: "5%",
            containLabel: true,
          },
          xAxis: [
            {
              type: "value",
              max: series.reduce((prev, current)=>prev+current.data[0],0),
              axisLine: {
                show: true,
                lineStyle: {
                  color: "#60676F",
                  width: 1,
                },
              },
              axisLabel: {
                color: "#60676F",
                fontSize: 12,
              },
              splitLine: {
                show: false,
              },
            },
          ],
          yAxis: [
            {
              type: "category",
              data: [""],
              axisLine: {
                show: false,
              },
              axisTick: {
                show: false,
              },
            },
          ],
          series,
        };
      },
      immediate: true,
      deep: true,
    },
  },
};
</script>
<style lang="scss">
.age-formatter {
  .age-title{
    font-size: 12px;
  }
  .item{
    font-size: 12px;
    display: flex;
    align-items: center;
    .point{
      margin-left: 0;
    }
  }
}
</style>
<style lang="scss" scoped>
.top {
  display: flex;
  justify-content: space-between;
  .average {
    margin-left: 20px;
  }
}

.bottom {
  display: flex;
  justify-content: space-between;
  font-weight: 700;
  font-size: 14px;
  .key {
    font-size: 12px;
    display: flex;
    align-items: center;
  }
}
</style>
