<template>
  <div class="user-count chart-item">
    <div class="top">
      <div class="title">
        外卖用户总数
        <div class="sub-title">用户总数</div>
      </div>
      <div class="number">{{ userData.userToday | numFormat }}</div>
    </div>
    <div class="growth">
      <div class="lastday">每日增长率：{{ userData.userGrowthLastDay }}%</div>
      <div class="lastmonth">
        每月增长率：{{ userData.userGrowthLastMonth }}%
      </div>
    </div>
    <my-echart height="20px" :options="options"></my-echart>
  </div>
</template>

<script>
import MyEchart from "@/components/MyEchart.vue";
export default {
  name: "user-count",
  props: {
    userData: {
      type: Object,
      default: () => ({}),
    },
  },
  components: {
    MyEchart,
  },
  data() {
    return {
      options: {},
    };
  },
  watch: {
    userData: {
      handler(newData) {
        this.options = {
          grid: {
            top: "5%",
            left: 0,
            right: 0,
            bottom: "5%",
          },
          xAxis: {
            show: false, //不显示x轴相关信息
          },
          yAxis: [
            {
              type: "category",
              axisLine: {
                show: false, //不显示轴线
              },
              axisTick: {
                show: false, //不显示刻度线
              },
            },
            {
              type: "category",
              axisLine: {
                show: false, //不显示轴线
              },
              axisTick: {
                show: false, //不显示刻度线
              },
            },
          ],
          series: [
            {
              name: "2011年",
              type: "bar",
              data: [newData.userGrowthLastDay],
              yAxisIndex: 0,
              // barWidth: 10, //柱子宽度
              itemStyle: {
                color: "#969696",
              },
            },
            {
              name: "2012年",
              type: "bar",
              data: [100],
              // barWidth: 10,
              yAxisIndex: 1,
              itemStyle: {
                color: "none",
                borderColor: "#969696",
                borderWidth: 2,
              },
            },
          ],
        };
      },
      immediate: true,
      deep: true,
    },
  },
};
</script>

<style lang="scss" scoped>
.user-count {
  .top{
    display: flex;
    justify-content: space-between;
  }
  .growth {
    color: #c5fb79;
    display: flex;
    font-size: 12px;
    .lastmonth {
      color: #63a900;
      margin-left: 10px;
    }
  }
}
</style>
