<template>
  <div class="area-sales chart-item">
    <div class="title">区域销售大盘环比分析</div>
    <dv-scroll-board :config="config" style="width:100%;height:400px" />
  </div>
</template>

<script>
export default {
  name: "area-sales",
  props: {
    areaSales: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      config: {},
    };
  },
  watch: {
    areaSales: {
      handler(newData) {
        let data = [];
        // 广州 -14%  广州 +18%  广州 -10%  广州 -4%  广州 -9%
        // avgOrder:"广州 -9%"  newShop:"广州 -4%"  order:"广州 -14%"  rider:"广州 -10%"  shop:"广州 +18%"
        newData.forEach((item) => {
          let arr = [
            `<span style="color:#FFF; font-size:12px">${item.order}</span>`,
            `<span style="color:#B2D17E; font-size:12px">${item.shop}</span>`,
            `<span style="color:#FFF; font-size:12px">${item.rider}</span>`,
            `<span style="color:#B2D17E; font-size:12px">${item.newShop}</span>`,
            `<span style="color:#FFF; font-size:12px">${item.avgOrder}</span>`,
          ];
          data.push(arr);
        });
        this.config = {
          header: ["订单量", "店铺数", "骑手", "新店铺", "人均订单"],
          data,
          rowNum: 10,
          index: true,
          columnWidth: [50],
          align: ["center"],
          headerBGC: "#5A5A5A",
          oddRowBGC: "#373737",
          evenRowBGC: "#282828",
          waitTime: 5000,
          headerHeight: 35,
          indexHeader: "序号",
          carousel: "single",
        };
      },
      immediate: true,
      deep: true,
    },
  },
};
</script>

<style lang="scss" scoped>
.area-sales {
  .title {
    text-align: center;
    margin-bottom: 10px;
  }
}
</style>
