<template>
  <div class="my-screen">
    <dv-loading v-if="loading">Loading...</dv-loading>
    <template v-else>
      <my-header></my-header>
      <main class="main">
        <div class="left">
          <user-count :user-data="userData"></user-count>
          <user-age :age-data="ageData"></user-age>
          <user-device :device-data="deviceData"></user-device>
          <user-gender :gender-data="genderData"></user-gender>
          <rider-data :rider-data="riderData"></rider-data>
          <hot-category :category-data="categoryData"></hot-category>
        </div>
        <div class="right-box">
          <top-header 
            :sales="salesData.salesToday" 
            :order="orderData.orderToday"
            :order-user="orderData.orderUser"
            :today-user="userData.userToday"
            :covert-rate="covertRate"
            :return-rate="returnRate"
          ></top-header>
          <div class="bottom">
            <div class="center">
              <map-data :geo-json="mapData"></map-data>
              <real-time-order :real-time-order="realTimeOrder"></real-time-order>
              <calendar-data></calendar-data>
            </div>
            <div class="right">
              <area-sales :area-sales="areaSales"></area-sales>
              <area-top :area-top="areaTop"></area-top>
            </div>
          </div>
        </div>
      </main>
    </template>
  </div>
</template>

<script>
import { screenDataApi, screenMapApi } from "@/api/api";
// 头部
import MyHeader from "@/components/MyHeader.vue";
// 左侧
import UserCount from "@/components/left/UserCount.vue";
import UserAge from "@/components/left/UserAge.vue";
import UserDevice from "@/components/left/UserDevice.vue";
import UserGender from "@/components/left/UserGender.vue";
import RiderData from "@/components/left/RiderData.vue";
import HotCategory from "@/components/left/HotCategory.vue";
// 中间
import TopHeader from "@/components/center/TopHeader.vue";
import MapData from "@/components/center/MapData.vue";
import CalendarData from "@/components/center/CalendarData.vue";
import realTimeOrder from "@/components/center/realTimeOrder.vue";
// 右侧
import AreaSales from "@/components/right/AreaSales.vue";
import AreaTop from "@/components/right/AreaTop.vue";

export default {
  name: "my-screen",
  components: {
    MyHeader,
    UserCount,
    UserAge,
    UserDevice,
    UserGender,
    RiderData,
    HotCategory,
    TopHeader,
    MapData,
    CalendarData,
    realTimeOrder,
    AreaSales,
    AreaTop,
  },
  data() {
    return {
      loading: true,
      ageData: {}, //年龄数据
      userData: {}, //用户数据
      deviceData: {}, //设备数据
      orderData: {}, //订单数据
      salesData: {}, //售卖数据
      genderData: [], //性别数据
      riderData: {}, //骑手数据
      categoryData: {}, //热卖数据
      areaSales: [], //区域销售数据
      areaTop: [], //区域排名数据
      mapData: {}, //地图数据
      covertRate:'',
      returnRate:'',
      realTimeOrder:'',
    };
  },
  created() {
    // setTimeout(() => {
      this.getData();
    // }, 3000*Math.random());
  },
  methods: {
    async getData() {
      let res1 = await screenDataApi();
      console.log(res1.data);
      this.covertRate = res1.data.covertRate;
      this.returnRate = res1.data.returnRate;
      this.realTimeOrder = res1.data.realTimeOrder;
      this.ageData = { //年龄数据
        ages: res1.data.age,
        averageAge: res1.data.averageAge,
      };
      this.userData = {
        userToday: res1.data.userToday, //外卖用户总数
        userLastDay: res1.data.userLastDay,
        userLastMonth: res1.data.userLastMonth,
        userGrowthLastDay: res1.data.userGrowthLastDay,
        userGrowthLastMonth: res1.data.userGrowthLastMonth,
        userFullYear: res1.data.userFullYear,
        userFullYearAxis: res1.data.userFullYearAxis,
        userRank: res1.data.userRank,
      };
      this.deviceData = {
        totalDevices: res1.data.totalDevices,
        devices: res1.data.devices,
      };
      this.genderData = res1.data.gender;
      this.riderData = res1.data.rider;
      this.categoryData = res1.data.category;
      this.areaSales = res1.data.areaSales;
      this.areaTop = res1.data.areaTop;
      this.salesData = {
        salesToday: res1.data.salesToday,
        salesLastDay: res1.data.salesLastDay,
        salesLastMonth: res1.data.salesLastMonth,
        salesGrowthLastDay: res1.data.salesGrowthLastDay,
        salesGrowthLastMonth: res1.data.salesGrowthLastMonth,
      };
      this.orderData = {
        orderFullYear: res1.data.orderFullYear,
        orderFullYearAxis: res1.data.orderFullYearAxis,
        orderGrowthLastDay: res1.data.orderGrowthLastDay,
        orderGrowthLastMonth: res1.data.orderGrowthLastMonth,
        orderLastDay: res1.data.orderLastDay,
        orderLastMonth: res1.data.orderLastMonth,
        orderPrice: res1.data.orderPrice,
        orderRank: res1.data.orderRank,
        orderToday: res1.data.orderToday,
        orderTrend: res1.data.orderTrend,
        orderUser: res1.data.orderUser,
        orderUserTrend: res1.data.orderUserTrend,
        orderUserTrendAxis: res1.data.orderUserTrendAxis,
        realTimeOrder: res1.data.realTimeOrder,
      };
      let res2 = await screenMapApi();
      console.log(res2.data);
      this.mapData = res2.data;

      this.loading = false;
    },
  },
};
</script>

<style lang="scss">
.my-screen{
  height: 100%;
  width: 100%;
  background-color: #241f20;
  color: #fff;
}
.main {
  .chart-item {
    background-color: #2b2c2e;
    padding: 10px 10px;
    border-bottom: 10px solid #3c3d40;
  }
  .title {
    font-size: 18px;
    .sub-title {
      font-size: 12px;
    }
  }
  .number {
    font-size: 26px;
    margin: 5px 0;
    font-weight: 700;
  }
  .point {
    display: inline-block;
    width: 8px;
    height: 8px;
    margin: 5px;
    margin-left: 0;
    border-radius: 50%;
  }
  .sub-text {
    font-size: 12px;
  }
}
</style>

<style lang="scss" scoped>
.main {
  display: flex;
  .left {
    flex-basis: 22%;
  }
  .right-box {
    flex-basis: 78%;
    margin-left: 10px;
    .bottom {
      display: flex;
      .center {
        flex-basis: 62%;
      }
      .right {
        flex-basis: 38%;
        margin-left: 10px;
      }
    }
  }
}
</style>
